import {library} from '@fortawesome/fontawesome-svg-core'
import {faTrash} from '@fortawesome/free-solid-svg-icons'
import {faEdit} from '@fortawesome/free-regular-svg-icons'

library.add(faTrash)
library.add(faEdit)
export default {
  layout: 'AdminTemplate',
  name: 'FAQManager',

  data() {
    return {
      perPage: 20,
      currentPage: 1,
      totalRows: 0,
      data: [],
      id: 0,
      isEnable: false,
      content: "",
      sortNumber: "",
      question: "",
      answer: "",
      search: {
        question: '',
      },
    }
  },
  mounted() {
    this.getAll()
  },
  methods: {
    getAll() {
      let sendData = {
        perPage: this.perPage,
        currentPage: this.currentPage,
        search: this.search,
      }

      this.$axios.post('/faq/getAll', sendData).then((res) => {
        this.totalRows = res.data.total
        this.data = res.data.rows
        console.log(this.data)
      }).catch(error => {
        //               this.$router.push('/login')
        console.log(error.response)
      })
    },

    clearForm() {
      this.id = 0
      this.isEnable = false
      this.answer = ''
      this.sortNumber = ''
      this.question = ''
    },
    saveFaq() {
      var formData = {
        'id': this.id,
        'isEnable': this.isEnable,
        'question': this.question,
        'sortNumber': this.sortNumber,
        'answer': this.answer,
      }
      this.$axios.post('/faq/create', formData).then((res) => {
        this.id = res.data.id
        this.clearForm()
        this.getAll()
      }).catch((error) => {
        if (error.response.data.errors) {
          // console.log(JSON.stringify(error.response.data.errors))
          this.errors = error.response.data.errors
        }
        console.log(error)
      })
    },

    deleteFaq(item) {

      this.$bvModal.msgBoxConfirm(this.$t('delete_confirm'), {
        title: 'Please Confirm',
        size: 'sm',
        buttonSize: 'sm',
        okVariant: 'danger',
        okTitle: 'YES',
        cancelTitle: 'NO',
        headerClass: 'p-2 border-bottom-0',
        footerClass: 'p-2 border-top-0',
        hideHeaderClose: false,
        centered: true
      }) .then(value => {
          this.delComfirm = value

          if (value === true) {
            if (this.id == item.id)
              this.id = 0

            this.$axios.post('/faq/deleteFaq', {'id': item.id}).then(() => {
              this.getAll()
            } ).catch(error => {
              console.log(error)
            })
          }
      }).catch(error => {
        console.log(error)
      })
    },

    editFaq(item) {
      this.clearForm()

      this.$axios.post('/faq/getFaqById', {'id': item.id}).then((res) => {
        console.log(res.data)
        this.id = res.data.id
        this.isEnable = res.data.isEnable
        this.answer = res.data.answer
        this.question = res.data.question
        this.sortNumber = res.data.sortNumber

      }).catch(error => {
        console.log(error.response)
      })

    },
  },

}
